/*================================================
Default btn CSS
=================================================*/
.default-btn {
  display: inline-block;
  padding: 12px 35px;
  color: #ffffff;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  transition: 0.5s;
}
.default-btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: linear-gradient(to right top, #ff4520, #f33d18, #df2f0c, #d42b0a, #C82506);
  transition: 0.5s;
}
.default-btn::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #C82506;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
  z-index: -1;
}
.default-btn:hover {
  color: #ffffff;
}
.default-btn:hover::after {
  opacity: 1;
  visibility: visible;
}

/*================================================
Section Title CSS
=================================================*/
.section-title {
  text-align: center;
  margin-bottom: 45px;
}
.section-title span {
  color: #df2f0c;
  font-weight: bold;
  text-transform: uppercase;
}
.section-title h2 {
  font-size: 48px;
  margin: 14px 0 25px 0;
}
.section-title .bar {
  height: 4px;
  width: 85px;
  background: linear-gradient(to right top, #ff4520, #f33d18, #df2f0c, #d42b0a, #C82506);
  position: relative;
  border-radius: 5px;
  overflow: hidden;
  margin: auto;
}
.section-title .bar::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  height: 100%;
  width: 5px;
  background: #ffffff;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-name: MOVE-BG;
}
.section-title.text-width {
  max-width: 600px;
  margin: 0 auto 45px;
}

@keyframes MOVE-BG {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(85px);
  }
}
/*================================================
Preloader Area CSS
=================================================*/
.preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 99999;
  background: linear-gradient(to right top, #ff4520, #f33d18, #df2f0c, #d42b0a, #C82506);
  top: 0;
  left: 0;
}
.preloader .loader {
  position: absolute;
  top: 43%;
  left: 0;
  right: 0;
  transform: translateY(-43%);
  text-align: center;
  margin: 0 auto;
  width: 50px;
  height: 50px;
}
.preloader .box {
  width: 100%;
  height: 100%;
  background: #ffffff;
  animation: animate 0.5s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 3px;
}
.preloader .shadow {
  width: 100%;
  height: 5px;
  background: #C82506;
  opacity: 0.1;
  position: absolute;
  top: 59px;
  left: 0;
  border-radius: 50%;
  animation: shadow 0.5s linear infinite;
}

@keyframes loader {
  0% {
    left: -100px;
  }
  100% {
    left: 110%;
  }
}
@keyframes animate {
  17% {
    border-bottom-right-radius: 3px;
  }
  25% {
    transform: translateY(9px) rotate(22.5deg);
  }
  50% {
    transform: translateY(18px) scale(1, 0.9) rotate(45deg);
    border-bottom-right-radius: 40px;
  }
  75% {
    transform: translateY(9px) rotate(67.5deg);
  }
  100% {
    transform: translateY(0) rotate(90deg);
  }
}
@keyframes shadow {
  50% {
    transform: scale(1.2, 1);
  }
}
